<template>
  <div class="custom">
    <p>Instance: <span v-text="instance.id" /></p>
    <p>Roles:</p>
    <p>
      <li v-for="item in roles" :key="item" @click="showRoleInfo(item)">
        {{ item }}
      </li>
    </p>
    <p>
      <span v-text="roleInfo" />
    </p>
  </div>
</template>

<script>
export default {
  props: {
      instance: {
          type: Object,
          required: true
      }
  },
  data: () => ({
      roles: [],
      roleInfo: '',
  }),
  async created() {
      const response = await this.instance.axios.get('actuator/role');
      this.roles = response.data;
  },
  mounted() {
  },
  methods: {
    async showRoleInfo (name) {
      const response = await this.instance.axios.get('actuator/role/' + name);
      this.roleInfo = response.data;
    }
  }
};
</script>

<style lang="css" scoped>
.custom {
  font-size: 20px;
  width: 80%;
}
</style>